<template>
	<view>
		<uni-header class="header" icon='plusempty' :onIconClick="onIconClick">创建打卡</uni-header>
		<scroll-view scroll-y="true" class="sv" :style="{height:scrollHeight+'px'}">
			<view v-for="goal in goals">
				<view class="goal">
					<text class="goal__goalName">{{goal.goal_name}}</text>
					<view class="goal__daysPersist">
						<view class="goal__daysPersist__days">1天</view>
						<view>已坚持</view>
					</view>
				</view>
			</view>
		</scroll-view>
		
	</view>
</template>

<script>
	import UniHeader from '../../components/uni-header.vue'
	export default {
		components:{
			UniHeader
		},
		onReady() {
			const that = this
			uni.getSystemInfo({
				success(res) {
					const headerEl = uni.createSelectorQuery().in(that).select('.header')
					headerEl.boundingClientRect(data => {
						that.scrollHeight = res.windowHeight - data.height
					}).exec()
				}
			})
		},
		onLoad() {
			uniCloud.callFunction({
				name: 'get_goal',
				data: {
					user_id: '1'
				}
			}).then(({ result }) => {
				this.goals = result.data
			})
		},
		data() {
			return {
				goals: [],
				scrollHeight: 0
			}
		},
		methods: {
			onIconClick() {
				uni.navigateTo({
					url: '/pages/create-goal/create-goal'
				})
			}
		}
	}
</script>

<style>
	.goal {
		box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.1);
		background-color: white;
		margin: 10px;
		padding: 12px 24px;
		display: flex;
	}
	.goal__goalName {
		line-height: 51px;
		vertical-align: baseline;
		flex: 1;
	}
	.goal__daysPersist {
		color: rgba(0,0,0,0.25);
		text-align: center;
	}
	.goal__daysPersist__days {
		color: black;
		font-size: 1.3em;
	}
	.sv {
		margin-top: 46px;
	}
</style>
